import React, {useEffect, useRef, useState} from 'react';
import s from './Main.module.scss';
import ReactTypingEffect from "react-typing-effect";




const Fade = require('react-reveal/Fade')


export default function Main() {

    const [vantaEffect, setVantaEffect] = useState<any>(0)
    const myRef = useRef(null)

    useEffect(() => {
        if (!vantaEffect) {
            setVantaEffect(BIRDS({
                el: myRef.current,
                mouseControls: true,
                touchControls: true,
                gyroControls: false,
                minHeight: 200.00,
                minWidth: 200.00,
                points: 14.00,
                maxDistance: 30.00,
                spacing: 20.00,
                scaleMobile: 1.00,
                color: '#a9c96a',
                backgroundColor: '#7766a1'
            }))
        }
        return () => {
            if (vantaEffect) vantaEffect.destroy()
        }
    }, [vantaEffect])

    return (
        <div className={s.mainBlock} id={'main'} ref={myRef}>
            <div className={s.container}>
                <Fade left>
                    <div className={s.greeting}>
                        <span>Hi, I'm</span>
                        <span>Kirill<span>Muraviev</span></span>
                        <span> <ReactTypingEffect text={'Front-end Developer'}/></span>
                    </div>
                    </Fade>
                        <div className={s.photo}>
                            <div className={s.image}>
                            </div>
                        </div>

            </div>
        </div>
    );
};
function BIRDS(arg0: { el: null; mouseControls: boolean; touchControls: boolean; gyroControls: boolean; minHeight: number; minWidth: number; points: number; maxDistance: number; spacing: number; scaleMobile: number; color: string; backgroundColor: string; }): any {
    throw new Error('Function not implemented.');
}

